"use client";

import { useEffect, useState } from "react";
import { supabase } from "@/lib/supabase";

export default function ManageProperties() {
  const [owners, setOwners] = useState<any[]>([]);
  const [areas, setAreas] = useState<any[]>([]);
  const [properties, setProperties] = useState<any[]>([]);

  const [selectedOwner, setSelectedOwner] = useState("");
  const [name, setName] = useState("");
  const [type, setType] = useState<"hotel" | "villa">("hotel");
  const [area, setArea] = useState("");
  const [price, setPrice] = useState("");

  useEffect(() => {
    fetchOwners();
    fetchAreas();
  }, []);

  const fetchOwners = async () => {
    const { data } = await supabase
      .from("profiles")
      .select("id, email")
      .eq("is_owner", true);

    setOwners(data || []);
  };

  const fetchAreas = async () => {
    const { data } = await supabase.from("areas").select("*");
    setAreas(data || []);
  };

  const fetchProperties = async (ownerId: string) => {
    const hotels = await supabase
      .from("hotels")
      .select("*")
      .eq("owner_id", ownerId);

    const villas = await supabase
      .from("villas")
      .select("*")
      .eq("owner_id", ownerId);

    setProperties([
      ...(hotels.data || []).map((p) => ({ ...p, type: "hotel" })),
      ...(villas.data || []).map((p) => ({ ...p, type: "villa" })),
    ]);
  };

  const createProperty = async () => {
    if (!name || !area || !price || !selectedOwner)
      return alert("Fill all fields");

    const payload = {
      name,
      owner_id: selectedOwner,
      area_id: area,
      starting_price: Number(price),
    };

    if (type === "hotel") {
      await supabase.from("hotels").insert(payload);
    } else {
      await supabase.from("villas").insert(payload);
    }

    setName("");
    setPrice("");
    fetchProperties(selectedOwner);
  };

  return (
    <main className="p-10 max-w-3xl space-y-6">
      <h1 className="text-2xl font-bold">Manage Properties</h1>

      {/* OWNER FILTER */}
      <select
        className="border p-2 w-full"
        value={selectedOwner}
        onChange={(e) => {
          setSelectedOwner(e.target.value);
          fetchProperties(e.target.value);
        }}
      >
        <option value="">Select Owner (by email)</option>
        {owners.map((o) => (
          <option key={o.id} value={o.id}>
            {o.email}
          </option>
        ))}
      </select>

      {selectedOwner && (
        <>
          {/* ADD PROPERTY */}
          <div className="border rounded-xl p-6 space-y-4">
            <h2 className="font-semibold">Add Property</h2>

            <input
              className="border p-2 w-full"
              placeholder="Property name"
              value={name}
              onChange={(e) => setName(e.target.value)}
            />

            <select
              className="border p-2 w-full"
              value={type}
              onChange={(e) => setType(e.target.value as any)}
            >
              <option value="hotel">Hotel</option>
              <option value="villa">Villa</option>
            </select>

            <select
              className="border p-2 w-full"
              value={area}
              onChange={(e) => setArea(e.target.value)}
            >
              <option value="">Select Area</option>
              {areas.map((a) => (
                <option key={a.id} value={a.id}>
                  {a.name}
                </option>
              ))}
            </select>

            <input
              className="border p-2 w-full"
              placeholder="Starting price"
              value={price}
              onChange={(e) => setPrice(e.target.value)}
            />

            <button
              onClick={createProperty}
              className="bg-black text-white px-4 py-2 rounded"
            >
              Add Property
            </button>
          </div>

          {/* PROPERTY LIST */}
          <div className="space-y-3">
            {properties.map((p) => (
              <div
                key={p.id}
                className="border p-4 rounded flex justify-between"
              >
                <div>
                  <div className="font-medium">
                    {p.name} ({p.type})
                  </div>
                  <div className="text-sm text-gray-500">
                    â‚¹{p.starting_price}
                  </div>
                </div>
              </div>
            ))}
          </div>
        </>
      )}
    </main>
  );
}
